'use client';
import { useEffect } from 'react';

export const Navigation = () => {
  useEffect(() => {
    const listItems = document.querySelectorAll('#landing-header li');
    const menuBackdrop = document.querySelector(
      '#menu-backdrop',
    ) as HTMLElement | null;

    if (!menuBackdrop || listItems.length === 0) {
      console.warn(
        'No se encontrÃ³ el elemento de fondo o los elementos de la lista.',
      );
      return;
    }

    const handleMouseEnter = (event: Event) => {
      const item = event.currentTarget as HTMLElement;
      const { left, top, width, height } = item.getBoundingClientRect();
      console.log({ left, width, height, top });
      menuBackdrop.style.setProperty('--left', `${left}px`);
      menuBackdrop.style.setProperty('--top', `${top}px`);
      menuBackdrop.style.setProperty('--width', `${width}px`);
      menuBackdrop.style.setProperty('--height', `${height}px`);
      menuBackdrop.style.opacity = '1';
      menuBackdrop.style.visibility = 'visible';
    };

    const handleMouseLeave = () => {
      menuBackdrop.style.opacity = '0';
      menuBackdrop.style.visibility = 'hidden';
    };

    listItems.forEach((item) => {
      item.addEventListener('mouseenter', handleMouseEnter as EventListener);
      item.addEventListener('mouseleave', handleMouseLeave as EventListener);
    });

    // Limpieza de eventos al desmontar el componente
    return () => {
      listItems.forEach((item) => {
        item.removeEventListener(
          'mouseenter',
          handleMouseEnter as EventListener,
        );
        item.removeEventListener(
          'mouseleave',
          handleMouseLeave as EventListener,
        );
      });
    };
  }, []);

  return (
    <header
      id="landing-header"
      className="p-inline sticky top-0 z-50 flex w-full flex-col items-center justify-between py-4 font-semibold tracking-wide md:flex-row"
    >
      <div className="flex py-2 text-2xl [&>*]:inline-block">
        Anna
        <span className="text-2xl text-[#A5FFD6]">.</span>
      </div>
      <nav>
        <ul className="flex [&>li>a]:inline-block [&>li>a]:px-5 [&>li>a]:py-2">
          <li>
            <a href="#">Galeria</a>
          </li>
          <li>
            <a href="#">Videos</a>
          </li>
          <li>
            <a href="#">Servicios</a>
          </li>
          <li>
            <a href="#">Testimonios</a>
          </li>
        </ul>
      </nav>
      <div
        id="menu-backdrop"
        className="absolute left-0 -z-10 h-[var(--height)] w-[var(--width)] translate-x-[var(--left)] rounded bg-[#6874E8]/10 opacity-0 backdrop-blur-lg transition-all duration-300 ease-in-out"
      ></div>
      <div className="flex justify-end py-2">
        <a
          className="transition-border border-b-2 border-transparent duration-200 hover:border-black"
          href="#"
        >
          Trabajemos juntos!
        </a>
      </div>
    </header>
  );
};
